/**
 * WordPress dependencies
 */
import { CardBody } from '@wordpress/components';
import { __ } from '@wordpress/i18n';

export default function ImageField( props ) {
	const { field, fieldKey, cardClasses } = props;

	return (
		<CardBody key={ fieldKey } className={ cardClasses }>
			<img
				key={ fieldKey }
				className="b2-image"
				src={ field.src }
				alt={ field.alt }
			/>
		</CardBody>
	);
}
