/**
 * External dependencies
 */
import { __ } from '@wordpress/i18n';
import { addQueryArgs } from '@wordpress/url';
import {
	Button,
	Card,
	CardBody,
	CardFooter,
	CheckboxControl,
	FlexItem as MaybeFlexItem,
	Spinner,
	Notice,
} from '@wordpress/components';
import { Component } from '@wordpress/element';
import { isEmpty, filter, map, has, includes, find } from 'lodash';
import axios from 'axios';
import classNames from 'classnames';
import parse from 'html-react-parser';
import { applyFilters } from '@wordpress/hooks';
import { SetupWizardSettings, getParsedAjaxError } from '../utilities'
import WizardHeader from '../components/header';
import ProductLabel from '../components/product-label'
import PluginsModal from '../components/plugins-modal';

// FlexItem is not available until WP version 5.5. This code is safe to remove
// once the minimum WP supported version becomes 5.5.
const FlextItemSubstitute = ( { children, align } ) => {
	const style = {
		display: 'flex',
		'justify-content': align ? 'center' : 'flex-start',
	};
	return <div style={ style }>{ children }</div>;
};
const FlexItem = MaybeFlexItem || FlextItemSubstitute;

class WithPlugins extends Component {

	constructor( props ) {
		super( props );
		this.openModal = this.openModal.bind( this );
		this.closeModal = this.closeModal.bind( this );

		const { dataStore } = props

		this.state = {
			is_loading: false,
			error_message: false,
			plugins: [],
			selected: applyFilters( 'barn2_selected_upsells', [], dataStore ),
			modal_is_open: false,
			license_key: false,
		}
	}

	/**
	 * On page load, get the plugins list via ajax.
	 */
	componentDidMount() {

		this.setIsLoading( true );
		this.setErrorMessage( false );

		let formData = new FormData()

		const Handler = this

		formData.append( 'action', `barn2_wizard_${ SetupWizardSettings.plugin_slug }_get_upsells` )
		formData.append( 'nonce', SetupWizardSettings.nonce_upsells )

		axios.post( SetupWizardSettings.ajax, formData )
			.then(function (response) {
				if ( has( response.data, 'data' ) ) {
					Handler.setState( {
						plugins: response.data.data.upsells,
						license_key: response.data.data.license,
					} )
				}
			})
			.catch(function (error) {
				if ( error.response ) {
					if ( ! isEmpty( getParsedAjaxError( error.response ) ) ) {
						Handler.setErrorMessage( getParsedAjaxError( error.response ) );
					} else {
						Handler.setErrorMessage( error.response.statusText );
					}
				} else if (error.request) {
					Handler.setErrorMessage( __( 'The request was made but no response was received.' ) );
				} else {
					Handler.setErrorMessage( __( 'Something went wrong while making the request.' ) );
				}
			})
			.then(function (response) {
				Handler.setIsLoading(false)
			});

	}

	/**
	 * Mark the app as loading.
	 *
	 * @param {} submitting
	 * @returns
	 */
	setIsLoading = submitting =>
		this.setState( state => ({
			is_loading: submitting
		})
	);

	/**
	 * Show an error message or disable the message.
	 *
	 * @param {} message
	 * @returns
	 */
	setErrorMessage = message =>
		this.setState( state => ({
			error_message: message
	  	})
	);

	/**
	 * Open the modal.
	 */
	openModal() {
		this.setState( { modal_is_open: true } );
	}

	/**
	 * Close the modal
	 */
	closeModal() {
		this.setState( { modal_is_open: false } );
	}

	/**
	 * Determine if the modal should open or go to the next page.
	 */
	handleNextStep() {
		const {
			selected,
		} = this.state

		if ( isEmpty( selected ) ) {
			this.props.goToNextStep()
		} else {
			this.openModal()
		}
	}

	/**
	 * Save the selected plugins into the app's state.
	 *
	 * @param {*} slug
	 */
	onChange( slug ) {
		this.setState(
			( state ) => {

				if ( includes( state.selected, slug ) ) {
					return {
						selected:
							filter( state.selected, ( value ) => {
								return value !== slug;
							} ) || [],
					};
				}

				const newSelected = state.selected;
				newSelected.push( slug );

				const accessPass = find( this.state.plugins, function(o) { return o.is_access_pass === '1' || o.is_access_pass === true; });

				if ( accessPass && includes( newSelected, accessPass.slug ) ) {
					return {
						selected: [ accessPass.slug ]
					}
				}

				return {
					selected: newSelected,
				};
			}
		);
	}

	render() {

		const {
			step,
			goToNextStep,
			goToPrevStep,
		} = this.props;

		const {
			is_loading,
			error_message,
			plugins,
			selected,
			modal_is_open,
			license_key,
		} = this.state

		const Handler = this

		const finishBtnLabel = ! isEmpty( selected ) ? __( 'Continue' ) : __( 'Finish setup' )

		const accessPass = find( plugins, function(o) { return o.is_access_pass === '1' || o.is_access_pass === true; });

		const utm = {
			utm_source: 'wizard',
			utm_medium: 'wizard',
			utm_campaign: `${SetupWizardSettings.utm_id}-wizard`,
			utm_content: `${SetupWizardSettings.utm_id}-wizard`,
		}

		return (
			<div className="woocommerce-profile-wizard__plugins">
				<div className="woocommerce-profile-wizard__step-header">
					<WizardHeader step={ step }/>
				</div>

				<Card>
					<CardBody size={ null }>

						{ is_loading && <div className="plugin-spinner"><Spinner></Spinner></div> }

						{ ! is_loading && ! isEmpty( error_message ) &&
							<Notice status={ 'error' } isDismissible={ false }>{ parse( error_message ) }</Notice>
						}

						{ ! is_loading && ! isEmpty( plugins ) &&
							<>
								{ map( plugins, function( plugin, index ) {

									const checkboxClasses = classNames(
										'woocommerce-profile-wizard__checkbox',
										'the-plugin-checkbox',
										{
											disabled_checkbox: accessPass !== undefined && plugin.slug !== accessPass.slug && selected.includes( accessPass.slug )
										}
									)

									const descClasses = classNames(
										'plugin-description',
										{
											disabled: accessPass !== undefined && plugin.slug !== accessPass.slug && selected.includes( accessPass.slug )
										}
									)

									const upsellClasses = classNames(
										'upsell-item',
										{
											access_pass_selected: accessPass !== undefined && plugin.slug !== accessPass.slug && selected.includes( accessPass.slug )
										}
									)

									const url = addQueryArgs( plugin.url, utm )

									return (
										<div key={ `${index}_plugin` } className={ upsellClasses }>
											<CheckboxControl
												label={ <ProductLabel
													plugin={ plugin }
													is_access_pass={ accessPass !== undefined && plugin.is_access_pass === '1' || plugin.is_access_pass === true }
													disabled={ accessPass !== undefined && plugin.slug !== accessPass.slug && selected.includes( accessPass.slug ) }
												/> }
												onChange={ () => Handler.onChange( plugin.slug ) }
												checked={ selected.includes( plugin.slug ) }
												disabled={ accessPass !== undefined && plugin.slug !== accessPass.slug && selected.includes( accessPass.slug ) }
												className={ checkboxClasses }
											/>
											{ ! isEmpty( plugin.description ) &&
												<span className={ descClasses }>
													{ plugin.description }
													{ ! isEmpty( plugin.url ) &&
														<span className="plugin-url">
															<a href={ url } target="_blank">{ __( 'Learn more' ) }</a>
														</span>
													}
												</span>
											}
										</div>
									)
								})}
							</>
						}
					</CardBody>

					{ ! is_loading &&
						<PluginsModal
							is_open={ modal_is_open }
							openModal={ this.openModal }
							closeModal={ this.closeModal }
							selected_plugins={ selected }
							goToNextStep={ goToNextStep }
							plugins={ plugins }
							{...this.props}
						/>
					}

					{ ! is_loading &&
						<CardFooter justify="center">
							<Button
									onClick={ () => goToPrevStep() }
									isSecondary
								>
									{ __( 'Back', 'woocommerce-admin' ) }
							</Button>
							<Button
								isPrimary
								onClick={ () => this.handleNextStep() }
								>
									{ finishBtnLabel }
							</Button>
						</CardFooter>
					}
				</Card>

			</div>
		);
	}
}

export default WithPlugins;
