/**
 * WordPress dependencies
 */
import { CardBody } from '@wordpress/components';
import { __ } from '@wordpress/i18n';
import { has, isEmpty } from 'lodash';

/**
 * WooCommerce dependencies
 */
import { SelectControl } from '@woocommerce/components';

/**
 * External dependencies
 */
import parse from 'html-react-parser';

/**
 * Internal dependencies
 */
import { SetupWizardSettings, generateUtmUrl } from '../utilities';
import FieldLabel from '../components/fieldLabel';

export default function SelectField( props ) {
	const {
		field,
		fieldKey,
		value,
		getInputProps,
		isSubmitting,
		cardClasses,
	} = props;

	const multiple = has( field, 'multiple' ) && field.multiple === true;
	const searchable = has( field, 'searchable' ) && field.searchable === true;

	// Adjust the label when multiple values can be selecteed so we change the "placeholder".
	let label = field.label;
	if ( ! isEmpty( value ) && has( field, 'alt_label' ) ) {
		label = field.alt_label;
	}

	return (
		<CardBody key={ fieldKey } className={ cardClasses }>
			{ has( field, 'premium' ) && (
				<FieldLabel label={ label } premium={ field.premium } />
			) }

			<SelectControl
				{ ...getInputProps( fieldKey ) }
				label={ label }
				options={ field.options }
				disabled={ field.premium || isSubmitting }
				multiple={ multiple }
				selected={ value || [] }
				isSearchable={ searchable }
			/>

			{ has( field, 'description' ) && ! isEmpty( field.description ) && (
				<p className="input-description">
					{ parse( field.description ) }
				</p>
			) }
		</CardBody>
	);
}
