/**
 * WordPress dependencies
 */
import { __ } from '@wordpress/i18n';

/**
 * External dependencies
 */
import classNames from 'classnames';

/**
 * Internal dependencies
 */
import { SetupWizardSettings, generateUtmUrl } from '../utilities';

export default function FieldLabel( props ) {
	const { label, premium, type } = props;

	const titleClass = classNames(
		'components-base-control__label',
		`${ type }-label`
	);

	return (
		<div className={ 'setup-wizard-label' }>
			{ label && <label className={ titleClass }>{ label }</label> }

			{ premium && SetupWizardSettings.premium_url && (
				<span className="components-base-control__label premium-label">
					{ ' - ' }
					<a
						href={ generateUtmUrl(
							SetupWizardSettings.premium_url
						) }
						target={ '_blank' }
					>
						{ __( 'Pro version only' ) }
					</a>
				</span>
			) }
		</div>
	);
}
