/**
 * External dependencies
 */
import { Component } from '@wordpress/element';
import { __ } from '@wordpress/i18n';
import { CardBody, Button, TextControl } from '@wordpress/components';
import { Icon, plus, minus } from '@wordpress/icons';
import { has, map, isEmpty } from 'lodash';

/**
 * External dependencies
 */
import parse from 'html-react-parser';

/**
 * Internal dependencies
 */
import FieldLabel from '../components/fieldLabel';
export default class TextRepeaterField extends Component {
	constructor( props ) {
		super( props );
		this.state = { values: [ '' ] };
	}

	/**
	 * Pre-load values if available.
	 */
	componentDidMount() {
		if ( ! isEmpty( this.props.value ) ) {
			this.setState( { values: this.props.value } );
		}
	}

	/**
	 * When the state is updated, push the values to the parent form.
	 *
	 * @param {*} prevProps
	 * @param {*} prevState
	 */
	componentDidUpdate( prevProps, prevState ) {
		if ( prevState.values !== this.state.values ) {
			this.props
				.getInputProps( this.props.fieldKey )
				.onChange( this.state.values );
		}
	}

	/**
	 * Save the input values into the state.
	 *
	 * @param {*} value
	 * @param {*} i
	 */
	handleChange( value, i ) {
		let values = [ ...this.state.values ];
		values[ i ] = value;
		this.setState( { values } );
	}

	/**
	 * Add a new field to the repeater
	 */
	addClick() {
		this.setState( ( prevState ) => ( {
			values: [ ...prevState.values, '' ],
		} ) );
	}

	/**
	 * Remove a specific input from the field.
	 * @param {*} i
	 */
	removeClick( i ) {
		let values = [ ...this.state.values ];
		values.splice( i, 1 );
		this.setState( { values } );
	}

	render() {
		const {
			field,
			fieldKey,
			getInputProps,
			isSubmitting,
			cardClasses,
		} = this.props;

		const { values } = this.state;

		const handler = this;

		return (
			<CardBody key={ fieldKey } className={ cardClasses }>
				<div className="barn2-repeater-field">
					<FieldLabel
						label={ field.label }
						premium={ field.premium }
						type={ field.type }
					/>
					{ map( values, function ( value, i ) {
						return (
							<div key={ i } className="individual-repeater">
								<div className="input-wrap">
									<TextControl
										value={ value || '' }
										disabled={
											field.premium || isSubmitting
										}
										onChange={ ( value ) =>
											handler.handleChange( value, i )
										}
									/>
								</div>

								<div>
									<Button
										isSecondary
										disabled={
											field.premium || isSubmitting
										}
										onClick={ () =>
											handler.removeClick( i )
										}
									>
										<Icon icon={ minus } />
									</Button>
								</div>
							</div>
						);
					} ) }

					<Button
						isSecondary
						onClick={ () => this.addClick() }
						disabled={ field.premium }
						className="add-psw"
					>
						<Icon icon={ plus } />
					</Button>
				</div>

				{ has( field, 'description' ) &&
					! isEmpty( field.description ) && (
						<p className="input-description">
							{ parse( field.description ) }
						</p>
					) }
			</CardBody>
		);
	}
}
