/**
 * External dependencies
 */
import { __ } from '@wordpress/i18n';
import { doAction } from '@wordpress/hooks';
import {
	Button,
	Popover,
} from '@wordpress/components';
import { Component } from '@wordpress/element';
import { Icon, info } from '@wordpress/icons';

import { SetupWizardSettings } from '../utilities'

export default class SetupWizardFooter extends Component {

	constructor( props ) {
		super( props );
		this.state = {
			skipText: __('Skip setup wizard'),
			popoverText: __('You can also set up the plugin on the settings page.'),
			isSkipVisible: true,
			isSkipTooltipVisible: true,
			isSkipSetupPopoverVisible: false,
		};
	}

	componentDidMount() {
		doAction('barn2_wizard_footer_mounted', this);
	}

	componentDidUpdate(prevProps) {
		if (this.props.step.key !== prevProps.step.key) {
			doAction('barn2_wizard_footer_updated', this);
		}
	}

	togglePopover() {
		this.setState(prevState => ({
			isSkipSetupPopoverVisible: !prevState.isSkipSetupPopoverVisible
		}));
	}

	render() {
		const {
			skipText,
			popoverText,
			isSkipVisible,
			isSkipTooltipVisible,
			isSkipSetupPopoverVisible,
		} = this.state;

		return (
			<div className="woocommerce-profile-wizard__footer woocommerce-profile-wizard__store-details">
				{ isSkipVisible &&
					<Button
						isLink
						className="woocommerce-profile-wizard__footer-link"
						href={ SetupWizardSettings.skip_url }
						onClick={ () => {
							this.setState( {
								showUsageModal: true,
							} );
							return false;
						} }
					>
						{ skipText }
					</Button>
				}

				{ isSkipVisible && isSkipTooltipVisible &&
					<>
						{ isSkipSetupPopoverVisible
							? <>
								<Button
									isTertiary
									label={ 'skip' }
									className="tooltip-btn"
									disabled
									onClick={ () => {
										this.togglePopover()
									} }
								>
									<Icon icon={ info } />
								</Button>
								<Popover
									focusOnMount="container"
									position="top center"
									onClose={ () =>
										{
											setTimeout(() => {
												this.togglePopover()
											}, 100);
										}
									}
								>
									{ popoverText }
								</Popover>
							</>
						
							: <Button
								isTertiary
								label={ 'skip' }
								className="tooltip-btn"
								onClick={ () => {
									this.togglePopover()
								} }
							>
								<Icon icon={ info } />
							</Button>
							
						}
					</>
				}
			</div>
		);
	}
}
