/**
 * External dependencies
 */
import { __ } from '@wordpress/i18n';
import { Button, Modal, Notice, Tooltip } from '@wordpress/components';
import { Pill } from '@woocommerce/components';
import { Component } from '@wordpress/element';
import { isEmpty, forEach, map, has, find } from 'lodash';
import { SetupWizardSettings, getAddToCartURL } from '../utilities';
import LicenseForm from '../forms/license';
import classNames from 'classnames';
import { addQueryArgs } from '@wordpress/url';

class PluginsModal extends Component {
	constructor( props ) {
		super( props );

		this.state = {
			open: false,
			error: false,
		};
	}

	/**
	 * When the props are updated. Detect if should open or close the modal.
	 * @param {*} prevProps
	 * @param {*} prevState
	 */
	componentDidUpdate( prevProps, prevState ) {
		const props = this.props;

		if ( props.is_open === true && prevProps.is_open === false ) {
			this.setState( { open: true } );
		} else if ( props.is_open === false && prevProps.is_open === true ) {
			this.setState( { open: false } );
		}
	}

	/**
	 * Get the list of plugins that have been selected.
	 * @returns array
	 */
	getPluginsList() {
		const plugins = this.props.plugins;
		const selected_plugins = this.props.selected_plugins;
		const plugins_list = [];

		forEach( plugins, function ( plugin ) {
			if ( selected_plugins.includes( plugin.slug ) ) {
				plugins_list.push( plugin );
			}
		} );

		return plugins_list;
	}

	/**
	 * Check if the access pass was selected.
	 *
	 * @returns bool
	 */
	accessPassSelected() {
		const { selected_plugins, plugins } = this.props;

		const accessPass = find( plugins, function ( o ) {
			return o.is_access_pass === true;
		} );

		if (
			accessPass !== undefined &&
			selected_plugins.includes( accessPass.slug )
		) {
			return true;
		}

		return false;
	}

	/**
	 * Determine how and if to proceed to the next step.
	 *
	 * @returns void
	 */
	maybeProceed() {
		const {
			selected_plugins,
			plugins,
			goToNextStep,
			dataStore,
		} = this.props;

		const { error } = this.state;

		const { licenseKey } = dataStore;

		if ( this.accessPassSelected() && isEmpty( licenseKey ) ) {
			this.setState( { error: true } );
			return;
		}

		if ( ! error && ! isEmpty( licenseKey ) ) {
			const purchaseURL = getAddToCartURL(
				selected_plugins,
				plugins,
				licenseKey
			);
			goToNextStep();
			window.open( purchaseURL, '_blank' );
			return;
		}

		const purchaseURL = getAddToCartURL(
			selected_plugins,
			plugins,
			licenseKey
		);

		goToNextStep();
		window.open( purchaseURL, '_blank' );
		return;
	}

	/**
	 * Validate the status of the license after is has been requested through the inline form.
	 *
	 * @param {*} data
	 */
	validateLicenseStatus( data ) {
		const { licenseKey, licenseStatus, licenseStatusText } = data;
		const { stateActions } = this.props;

		stateActions.updateLicenseDetails( {
			licenseKey: licenseKey,
			licenseStatus: licenseStatus,
			licenseStatusText: licenseStatusText,
		} );

		if (
			licenseStatus === 'active' ||
			licenseStatus === 'valid' ||
			licenseStatus === 'expired'
		) {
			this.setState( { error: false } );
		}
	}

	render() {
		const {
			selected_plugins,
			closeModal,
			goToNextStep,
			dataStore,
		} = this.props;

		const { licenseKey } = dataStore;

		const { open, error } = this.state;

		const modal_title = __(
			'Would you like to purchase and install the following features now?'
		);

		if ( isEmpty( selected_plugins ) || ! open ) {
			return <></>;
		} else {
			const list = this.getPluginsList();

			return (
				<>
					{ open && ! isEmpty( selected_plugins ) && (
						<Modal
							title={ modal_title }
							onRequestClose={ () => closeModal() }
						>
							{ ! isEmpty( list ) && (
								<>
									{ map( list, function ( plugin, i ) {
										const is_access_pass =
											( has( plugin, 'is_access_pass' ) &&
												plugin.is_access_pass ===
													'1' ) ||
											plugin.is_access_pass === true;

										const pluginBoxClasses = classNames(
											'plugin-box',
											{
												last_box: i === list.length - 1,
											}
										);

										const pluginURL = addQueryArgs(
											plugin.url,
											{
												utm_source: 'wizard',
												utm_medium: 'wizard',
												utm_campaign: `${ SetupWizardSettings.utm_id }-wizard`,
												utm_content: `${ SetupWizardSettings.utm_id }-wizard`,
											}
										);

										return (
											<div
												key={ plugin.slug }
												className={ pluginBoxClasses }
											>
												<a
													href={ pluginURL }
													target="_blank"
												>
													{ plugin.name }
												</a>
												{ has( plugin, 'price' ) &&
													! isEmpty(
														plugin.price
													) && (
														<Tooltip position="bottom center">
															<Pill>
																{ is_access_pass &&
																	__(
																		'Upgrade for'
																	) }
																{ is_access_pass &&
																	has(
																		plugin,
																		'original_price'
																	) &&
																	! isEmpty(
																		plugin.original_price
																	) && (
																		<span className="original-price">
																			{
																				plugin.original_price
																			}
																		</span>
																	) }
																{ plugin.price }
															</Pill>
														</Tooltip>
													) }
											</div>
										);
									} ) }
								</>
							) }

							{ error && this.accessPassSelected() && (
								<>
									<LicenseForm
										eventable={ true }
										onLicenseUpdate={ ( data ) =>
											this.validateLicenseStatus( data )
										}
										{ ...this.props }
									/>
								</>
							) }

							{ ! error && ! isEmpty( licenseKey ) && (
								<Notice
									status="success"
									isDismissible={ false }
									className="license-missing-modal-warning"
								>
									{ __(
										'You can now proceed with the upgrade.'
									) }
								</Notice>
							) }

							{ selected_plugins.length > 1 ? (
								<p>
									{ __(
										"You won't have access to these features until the extensions have been purchased and installed."
									) }
								</p>
							) : (
								<p>
									{ __(
										"You won't have access to this functionality until the extension has been purchased and installed."
									) }
								</p>
							) }

							<div className="barn2-modal-footer">
								<Button
									isTertiary
									onClick={ () => goToNextStep() }
								>
									{ __( "I'll do it later" ) }
								</Button>
								<Button
									isPrimary
									onClick={ () => this.maybeProceed() }
									disabled={ error }
								>
									{ __( 'Purchase & install now' ) }
								</Button>
							</div>
						</Modal>
					) }
				</>
			);
		}
	}
}

export default PluginsModal;
