/**
 * WordPress dependencies
 */
import { CardBody, RadioControl } from '@wordpress/components';
import { __ } from '@wordpress/i18n';
import { has, isEmpty } from 'lodash';

/**
 * External dependencies
 */
import parse from 'html-react-parser';

/**
 * Internal dependencies
 */
import FieldLabel from '../components/fieldLabel';

export default function RadioField( props ) {
	const { field, fieldKey, isSubmitting, cardClasses, value, setValue } = props;

	return (
		<CardBody key={ fieldKey } className={ cardClasses }>
			<RadioControl
				label={
					<FieldLabel
						label={ field.label }
						premium={ field.premium }
						type={ field.type }
					/>
				}
				disabled={ field.premium || isSubmitting }
				options={ field.options }
				selected={ value }
				onChange={ (value) => setValue( fieldKey, value ) }
			/>
			{ has( field, 'description' ) && ! isEmpty( field.description ) && (
				<p className="input-description">
					{ parse( field.description ) }
				</p>
			) }
		</CardBody>
	);
}
