import { has, map, isEmpty, find, forEach, get } from 'lodash';
import { addQueryArgs } from '@wordpress/url';

/**
 * Retrieve the setup wizard settings configured via php.
 */
const barn2PluginSettings = barn2_setup_wizard;

export { barn2PluginSettings as SetupWizardSettings };

/**
 * Retrieve the error message from an ajax response.
 * @param {object} response
 * @returns
 */
export const getParsedAjaxError = ( response ) => {
	let message = null;

	if ( has( response.data, 'success' ) && response.data.success === false ) {
		message = response.data.data.error_message;
	}

	return message;
};

/**
 * Generate an add to cart url to purchase plugins via the barn2 website.
 * @param {array} plugins
 * @returns
 */
export const getAddToCartURL = ( selected, plugins, license = false ) => {
	const url = 'https://barn2.com';
	let args = {};
	const toPurchase = [];
	const accessPass = find( plugins, function ( o ) {
		return o.is_access_pass === '1';
	} );

	if ( accessPass !== undefined && selected.includes( accessPass.slug ) ) {
		args = {
			upgrade_to_pass: true,
			from_product: barn2PluginSettings.plugin_product_id,
			license_key: license,
		};
	} else {
		forEach( selected, function ( slug, key ) {
			const pluginConfig = find( plugins, ( p ) => p.slug === slug );

			if ( pluginConfig ) {
				const id = parseInt( pluginConfig.product_id );
				const variation = get( pluginConfig, 'variation_id', false );
				toPurchase.push( { id: id, price_id: variation } );
			}
		} );
	}

	if ( toPurchase.length > 0 ) {
		args = { edd_bulk_cart: toPurchase };
	}

	args = {
		...args,
		utm_source: 'wizard',
		utm_medium: 'wizard',
		utm_campaign: `${ barn2PluginSettings.utm_id }-wizard`,
		utm_content: `${ barn2PluginSettings.utm_id }-wizard`,
	};

	return addQueryArgs( url, args );
};

/**
 * Add the UTM parmaters to a given URL.
 *
 * @param {string} url
 */
export const generateUtmUrl = ( url ) => {
	if ( barn2PluginSettings.utm_id.length === 0 ) {
		return url;
	}

	const utm = {
		utm_source: 'wizard',
		utm_medium: 'wizard',
		utm_campaign: `${ barn2PluginSettings.utm_id }-wizard`,
		utm_content: `${ barn2PluginSettings.utm_id }-wizard`,
	};

	return addQueryArgs( url, utm );
};

/**
 * Generate the list of initial values of all fields within the setup wizard.
 *
 * @returns array
 */
export const getWizardValues = () => {
	const values = {};

	map( barn2_setup_wizard.steps, function ( step, index ) {
		map( step.fields, function ( field, field_key ) {
			if ( has( field, 'value' ) ) {
				values[ field_key ] = field.value;

				if (
					( field.type === 'text' ||
						field.type === 'number' ||
						field.type === 'textarea' ) &&
					field.value === false
				) {
					values[ field_key ] = '';
				}

				if ( field.type === 'checkbox' && ! isEmpty( field.value ) ) {
					if (
						field.value === 'yes' ||
						field.value === true ||
						field.value === '1'
					) {
						values[ field_key ] = true;
					} else {
						values[ field_key ] = false;
					}
				}

				// For dropdown fields with multiple values, we must retrieve the entire object instead of just the key of selected values.
				if (
					field.type === 'select' &&
					field.multiple === true &&
					! isEmpty( field.value )
				) {
					values[ field_key ] = [];
					map( field.value, function ( selected_value ) {
						const theOption = find( field.options, function ( o ) {
							return o.key === selected_value;
						} );
						values[ field_key ].push( theOption );
					} );
				}
			} else {
				if ( field.type !== 'checkbox' ) {
					values[ field_key ] = '';
				}
			}
		} );
	} );

	return values;
};
