/**
 * WordPress dependencies
 */
import { Component } from '@wordpress/element';
import { __ } from '@wordpress/i18n';
import {
	CardBody,
	__experimentalNumberControl as NumberControl,
} from '@wordpress/components';
import { has, isEmpty } from 'lodash';
import { decodeEntities } from '@wordpress/html-entities';

/**
 * External dependencies
 */
import parse from 'html-react-parser';

/**
 * Internal dependencies
 */
import FieldLabel from '../components/fieldLabel';

export default class MinMaxField extends Component {
	constructor( props ) {
		super( props );

		this.state = {
			minValue: this.props.field?.min?.min_value ?? 0,
			maxValue: this.props.field?.min?.min_value ?? 0,
		};
	}

	/**
	 * Pre-load values if available.
	 */
	componentDidMount() {
		if ( ! isEmpty( this.props.value ) ) {
			this.setState( {
				minValue: this.props.value.min,
				maxValue: this.props.value.max,
			} );
		}
	}

	/**
	 * When the state is updated, push the values to the parent form.
	 *
	 * @param {*} prevProps
	 * @param {*} prevState
	 */
	componentDidUpdate( prevProps, prevState ) {
		if (
			prevState.minValue !== this.state.minValue ||
			prevState.maxValue !== this.state.maxValue
		) {
			this.props.formProps.onChange( {
				min: this.state.minValue,
				max: this.state.maxValue,
			} );
		}
	}

	render() {
		const { field, isSubmitting, cardClasses, fieldKey } = this.props;

		const { minValue, maxValue } = this.state;

		return (
			<CardBody className={ cardClasses }>
				{ has( field, 'title' ) && (
					<FieldLabel
						label={ decodeEntities( field.title ) }
						premium={ field.premium }
						type={ field.type }
					/>
				) }

				<div
					style={ {
						display: 'flex',
						justifyContent: 'space-between',
					} }
				>
					<NumberControl
						onChange={ ( value ) =>
							this.setState( { minValue: value } )
						}
						isShiftStepEnabled={ true }
						isDragEnabled={ false }
						shiftStep={ 10 }
						label={
							decodeEntities( field?.min?.label ) ??
							__( 'Minimum' )
						}
						disabled={ field.premium || isSubmitting }
						className={ 'muriel-component min-input' }
						value={ minValue }
						min={ field?.min?.min_value ?? -Infinity }
						max={ field?.min?.max_value ?? Infinity }
						step={ field?.step ?? 1 }
						key={ `${ fieldKey }_min` }
					/>
					<NumberControl
						onChange={ ( value ) =>
							this.setState( { maxValue: value } )
						}
						isShiftStepEnabled={ true }
						isDragEnabled={ false }
						shiftStep={ 10 }
						label={
							decodeEntities( field?.max?.label ) ??
							__( 'Maximum' )
						}
						disabled={ field.premium || isSubmitting }
						className={ 'muriel-component max-input' }
						value={ maxValue }
						min={ field?.max?.min_value ?? -Infinity }
						max={ field?.max?.max_value ?? Infinity }
						step={ field?.step ?? 1 }
						key={ `${ fieldKey }_max` }
					/>
				</div>

				{ has( field, 'description' ) &&
					! isEmpty( field.description ) && (
						<p className="input-description">
							{ parse( field.description ) }
						</p>
					) }
			</CardBody>
		);
	}
}
