/**
 * External dependencies
 */
import { __, sprintf } from '@wordpress/i18n';
import { doAction } from '@wordpress/hooks';
import {
	Dashicon,
	Button,
	withFilters,
	SlotFillProvider,
	Slot,
	CardBody,
	__experimentalHeading as Heading
} from '@wordpress/components';
import { Component } from '@wordpress/element';
import { isEmpty } from 'lodash';
import parse from 'html-react-parser';
import { StyledList } from '@barn2media/components';
import { Icon, chevronRight } from '@wordpress/icons';
import { SetupWizardSettings } from '../utilities'

const AdditionalContent = withFilters(
	'barn2_setup_wizard_ready_page'
)( ( props ) => <></> );

class Ready extends Component {

	constructor( props ) {
		super( props );

		const { heading, description } = props.step;
		this.state = {
			heading,
			description,
			settingsButtonText: __('Settings page'),
			showSettingsButton: true,
		};
	}

	componentDidMount() {
		doAction( 'barn2_wizard_ready_mounted', this )
	}

	componentDidUpdated(prevProps) {
		if (this.props.step.key !== prevProps.step.key) {
			doAction( 'barn2_wizard_ready_updated', this )
		}
	}

	/**
	 * Get the list of signpost links to display on the page.
	 * Automatically add a settings page as last link.
	 *
	 * @returns {array}
	 */
	getSignposts() {

		const links = []

		if ( isEmpty( SetupWizardSettings.signpost ) ) {
			return links
		}

		SetupWizardSettings.signpost.forEach( element => {
			links.push( {
				after: <Icon icon={ chevronRight } />,
				...element
			} )
		});

		links.push( {
			after: <Icon icon={ chevronRight } />,
			title: __( 'Go to settings page' ),
			href: SetupWizardSettings.skip_url
		} )

		return links;

	}

	render() {
		const {
			heading,
			description,
			settingsButtonText,
			showSettingsButton,
		} = this.state;

		return (
			<SlotFillProvider>

				<AdditionalContent { ...this.props } />

				<div className="woocommerce-profile-wizard__store-details">
					<div className="woocommerce-profile-wizard__step-header">

						<Dashicon icon="yes-alt" className="completed-icon" />

						<Heading
							level="2"
							size="20"
							lineHeight="28px"
						>
							{ isEmpty( heading ) ? sprintf( __( 'Welcome to %s', '' ), SetupWizardSettings.plugin_name ) : heading }
						</Heading>

						{ ! isEmpty( description ) &&
							<div className="completed-desc">
								{ parse( description ) }
							</div>
						}

						<Slot name="ReadyPageContent" />

						{ ! isEmpty( this.getSignposts() ) &&
							<>
								<h2 className="what-next-text">{__('What next?')}</h2>
								<CardBody>
									<StyledList items={this.getSignposts()} />
								</CardBody>
							</>
						}

						{ showSettingsButton && isEmpty( this.getSignposts() ) &&
							<Button className="completed-btn" href={ SetupWizardSettings.skip_url } isSecondary>{ settingsButtonText }</Button>
						}

					</div>
				</div>

			</SlotFillProvider>
		);
	}
}

export default Ready
