/**
 * External dependencies
 */
import { __ } from '@wordpress/i18n';
import { applyFilters, doAction } from '@wordpress/hooks';
import React from 'react';
import {
	pick,
	forEach,
	get,
	findIndex,
	findLastIndex,
	filter,
	isEmpty
} from 'lodash';
import { Component, createElement } from '@wordpress/element';
import { Spinner, Notice } from '@wordpress/components';
import deprecated from '@wordpress/deprecated';
import { getQueryArg } from '@wordpress/url';

import { withMyHook } from './store';

/**
 * Internal dependencies
 */
import SetupWizardHeader from './components/breadcrumbs';
import SetupWizardFooter from './components/footer';
import { SetupWizardSettings, getWizardValues } from './utilities';

/**
 * Default steps
 */
import WelcomeFree from './steps/welcome-free';
import LicenseActivation from './steps/license-activation';
import WithForm from './steps/with-form';
import WithPlugins from './steps/with-plugins';
import Ready from './steps/ready';

/**
 * Import custom components so that they can be re-used by plugins.
 */
import ReusableHeader from './components/header';

/**
 * Display the setup wizard.
 */
export default class SetupWizard extends Component {
	constructor( props ) {
		super( props );
		this.goToNextStep = this.goToNextStep.bind( this );
		this.goToPrevStep = this.goToPrevStep.bind( this );
		this.showStep = this.showStep.bind( this );
		this.hideStep = this.hideStep.bind( this );
		this.goToStep = this.goToStep.bind( this );
		this.isFirstStep = this.isFirstStep.bind( this );
		this.isLastStep = this.isLastStep.bind( this );
		this.resetSteps = this.resetSteps.bind( this );
		this.getValues = this.getValues.bind( this );
		this.updateValue = this.updateValue.bind( this );

		let steps = [];

		// Get steps configured via php.
		const defaultSteps = SetupWizardSettings.steps;

		// Define a list of default page's view components
		const defaultStepsComponents = {
			welcome_free: WelcomeFree,
			license_activation: LicenseActivation,
			more: WithPlugins,
			ready: Ready,
		};

		const _this = this;

		// Push all steps into the state of the app with the proper view
		forEach( defaultSteps, function ( step, key ) {
			steps.push( {
				...step,
				key: step.key,
				container: get( defaultStepsComponents, step.key, WithForm ),
				label: step.label,
			} );
		} );

		// Make steps filterable by plugins
		steps = applyFilters( 'barn2_setup_wizard_steps', steps );

		// Wrap step containers in Little State Machine
		steps.forEach(
			( step ) => ( step.container = withMyHook( step.container ) )
		);

		this.state = {
			page: steps[ 0 ].key,
			steps: steps,
			values: getWizardValues(),
			wizard_completed: false,
			wizard_loading: false,
			error_message: false,
		};
	}

	componentDidMount() {
		const urlQuery = getQueryArg( window.location.href, 'step');

		if ( ! isEmpty( urlQuery ) && urlQuery !== this.state.page ) {
			this.setState( { page: urlQuery } );
		}

		document.body.classList.remove( 'woocommerce-admin-is-loading' );
		document.body.classList.add( 'woocommerce-onboarding' );
		document.body.classList.add( 'woocommerce-profile-wizard__body' );
		document.body.classList.add( 'woocommerce-admin-full-screen' );
		document.body.classList.add( 'is-wp-toolbar-disabled' );

		// If the wizard was completed once, fire hook to detect what to do on restart.
		const completedOnce = SetupWizardSettings.completed;
		if ( completedOnce ) {
			this.restartFromCompleted();
		}
	}

	componentWillUnmount() {
		document.body.classList.remove( 'woocommerce-onboarding' );
		document.body.classList.remove( 'woocommerce-profile-wizard__body' );
		document.body.classList.remove( 'woocommerce-admin-full-screen' );
		document.body.classList.remove( 'is-wp-toolbar-disabled' );
	}

	/**
	 * Helper method to return the list of steps.
	 *
	 * @returns array
	 */
	getSteps() {
		return this.state.steps;
	}

	/**
	 * Return the current values of all the controls in the Setup Wizard
	 *
	 * @returns array
	 */
	getValues() {
		return this.state.values;
	}

	/**
	 * Update a single value in the state
	 *
	 * @returns array
	 */
	updateValue( key, value ) {
		const { values } = this.state;

		values[ key ] = value;
		this.setState( { values } );
	}

	/**
	 * Show a step.
	 *
	 * @param {string} slug
	 */
	showStep( slug ) {
		let step = this.getSteps().find( ( s ) => s.key === slug );
		let steps = this.getSteps();
		const indexOfStep = findIndex( this.getSteps(), function ( o ) {
			return o.key == slug;
		} );

		step.hidden = false;
		steps[ indexOfStep ] = step;

		this.setState( {
			steps: steps,
		} );
	}

	/**
	 * Hide a step.
	 *
	 * @param {string} slug
	 */
	hideStep( slug ) {
		let step = this.getSteps().find( ( s ) => s.key === slug );
		let steps = this.getSteps();
		const indexOfStep = findIndex( this.getSteps(), function ( o ) {
			return o.key == slug;
		} );

		step.hidden = true;
		steps[ indexOfStep ] = step;

		this.setState( {
			steps: steps,
		} );
	}

	/**
	 * Reset steps to their original status.
	 *
	 * This is used only when there are hidden steps that need to be re-hidden.
	 */
	resetSteps() {
		const defaultSteps = SetupWizardSettings.steps;
		let steps = this.state.steps;

		forEach( defaultSteps, function ( step ) {
			const hidden = step.hidden;
			const indexOfStep = findIndex( steps, function ( o ) {
				return o.key == step.key;
			} );

			if ( hidden === true ) {
				steps[ indexOfStep ].hidden = hidden;
			}
		} );

		this.setState( { steps: steps } );
	}

	/**
	 * Get the currently active step.
	 *
	 * @returns object
	 */
	getCurrentStep() {
		const step = this.state.page;
		const currentStep = this.getSteps().find( ( s ) => s.key === step );

		if ( ! currentStep ) {
			return this.getSteps()[ 0 ];
		}

		return currentStep;
	}

	/**
	 * Check whether a step exists and is visible.
	 *
	 * @param {string} slug
	 */
	hasStep( slug ) {
		const step = this.getSteps().find(
			( s ) => s.key === slug && ! s.hidden
		);

		return !! step;
	}

	/**
	 * Determine if the current step is the first step in the wizard.
	 * @returns bool
	 */
	isFirstStep() {
		const step_key = this.state.page;
		const step = findIndex( this.getSteps(), function ( o ) {
			return o.key == step_key;
		} );
		return 0 === step;
	}

	/**
	 * Determine if the current step is the last step in the wizard.
	 * @returns bool
	 */
	isLastStep() {
		const step_key = this.state.page;
		const step =
			findLastIndex( this.getSteps(), function ( o ) {
				return o.key == step_key;
			} ) + 1;
		return this.getSteps().length === step;
	}

	/**
	 * Mark all steps as completed or not.
	 * @param {bool} completed
	 */
	setStepsCompleted( completed ) {
		//const steps = this.state.steps;
		//forEach( this.getSteps(), function ( step, index ) {
		//	steps[ index ].isComplete = completed;
		//} );
		//this.setState( { steps: steps } );

		deprecated( 'setStepsCompleted', {
			since: '0.4.8',
			version: '1.0.0',
			alternative: false,
			plugin: 'the setup wizard library',
			hint: 'The setup wizard no longer requires to show steps as completed on restart - please remove any call to this function from your integrations.',
		} );
	}

	/**
	 * Navigate to the next page.
	 */
	async goToNextStep() {
		const steps = filter(
			this.getSteps(),
			( step ) => step.hidden !== true
		);

		const currentStep = this.getCurrentStep();
		const currentStepIndex = steps.findIndex(
			( s ) => s.key === currentStep.key
		);

		const nextStep = steps[ currentStepIndex + 1 ];

		if ( typeof nextStep === 'undefined' ) {
			return;
		} else {
			this.setState( { page: nextStep.key } );
		}
	}

	/**
	 * Navigate to the previous page.
	 */
	async goToPrevStep() {
		const steps = filter(
			this.getSteps(),
			( step ) => step.hidden !== true
		);

		const currentStep = this.getCurrentStep();
		const currentStepIndex = steps.findIndex(
			( s ) => s.key === currentStep.key
		);

		const prev = steps[ currentStepIndex - 1 ];

		if ( typeof prev === 'undefined' ) {
			return;
		} else {
			this.setState( { page: prev.key } );
		}
	}

	/**
	 * Go to a specific page.
	 *
	 * @param {*} slug
	 * @returns
	 */
	async goToStep( slug ) {
		if ( this.hasStep( slug ) ) {
			this.setState( { page: slug } );
		}
	}

	/**
	 * Set an error message while loading the wizard.
	 * @param {string} message
	 */
	setErrorMessage( message ) {
		this.setState( { error_message: message } );
	}

	/**
	 * Allow plugins to hook into the restart process.
	 */
	restartFromCompleted() {
		const wizard = this;
		doAction( `barn2_wizard_on_restart`, wizard );
	}

	/**
	 * Render the setup wizard and all of it's elements.
	 */
	render() {
		const { query } = this.props;
		const step = this.getCurrentStep();
		const stepKey = step.key;
		const container = createElement( step.container, {
			query,
			step,
			goToNextStep: this.goToNextStep,
			goToPrevStep: this.goToPrevStep,
			showStep: this.showStep,
			hideStep: this.hideStep,
			isFirstStep: this.isFirstStep,
			isLastStep: this.isLastStep,
			resetSteps: this.resetSteps,
			getValues: this.getValues,
			updateValue: this.updateValue,
			components: {
				header: ReusableHeader,
			},
		} );
		const steps = this.getSteps().map( ( _step ) =>
			pick( _step, [ 'key', 'label', 'isComplete', 'hidden' ] )
		);
		const classNames = `woocommerce-profile-wizard__container ${ stepKey }`;

		const { wizard_loading, error_message } = this.state;

		if ( wizard_loading ) {
			return (
				<>
					{ error_message && (
						<Notice
							status="error"
							className="barn2-global-error"
							isDismissible={ false }
						>
							{ error_message }
						</Notice>
					) }
					<div className="wizard-main-loader">
						<Spinner></Spinner>
					</div>
				</>
			);
		}

		return (
			<>
				<SetupWizardHeader
					navigate={ this.goToStep }
					currentStep={ stepKey }
					steps={ steps }
				/>
				<div className={ classNames }>
					{ container }
					{ stepKey !== 'ready' && (
						<SetupWizardFooter step={ step } />
					) }
				</div>
			</>
		);
	}
}
