/**
 * WordPress dependencies
 */
import { __ } from '@wordpress/i18n';
import { has } from 'lodash';

/**
 * External dependencies
 */
import parse from 'html-react-parser';
import classNames from 'classnames';

export default function HeadingField( props ) {
	const { field, fieldKey } = props;
	const Size = has( field, 'size' ) ? field.size : 'h3';
	const allowed = [ 'h1', 'h2', 'h3', 'h4', 'h5', 'h6', 'p' ];
	const headingClassNames = classNames( 'b2-wizard-heading', fieldKey );
	const style = has( field, 'style' ) ? field.style : {};

	return (
		<div className={ headingClassNames } key={ fieldKey }>
			{ allowed.includes( Size ) && (
				<Size style={ style }>{ parse( field.label ) }</Size>
			) }
		</div>
	);
}
