/**
 * External dependencies
 */
import { __, sprintf } from '@wordpress/i18n';
import {
	Button,
	Card,
	CardBody,
	CardFooter,
	Popover,
	Notice,
	TextControl,
	__experimentalText as Text,
	__experimentalHeading as Heading
} from '@wordpress/components';
import { Component } from '@wordpress/element';
import { Icon, info } from '@wordpress/icons';
import { isEmpty, has } from 'lodash';
import classNames from 'classnames';
import parse from 'html-react-parser';
import { getParsedAjaxError, SetupWizardSettings } from '../utilities'
import axios from 'axios';

class LicenseActivation extends Component {

	constructor(props) {
		super(props);

		this.state = {
			isStepDescriptionPopoverVisible: false,
			activeElement: null,
			loading: false,
			licensePopoverVisible: false,
			licenseError: false,
			licenseStatus: false,
			licenseStatusText: '',
			licenseKey: '',
		};
	}

	/**
	 * On mount, check if we have details into the datastore.
	 */
	componentDidMount() {
		this.getLicenseDetails()
	}

	/**
	 * Get license details via ajax.
	 */
	getLicenseDetails = () => {

		const { stateActions } = this.props

		this.setLoading( true )

		let formData = new FormData()

		formData.append('action', `barn2_wizard_${SetupWizardSettings.plugin_slug}_get_license_details`)
		formData.append('nonce', SetupWizardSettings.nonce)

		axios.post(SetupWizardSettings.ajax, formData)
			.then( (response) => {
				if ( response.data?.data?.license_key ) {
					this.setState( {
						licenseKey: response.data?.data?.license_key,
						licenseStatus: response.data?.data?.license_status,
						licenseStatusText: response.data?.data?.license_status_text
					} )
					stateActions.updateLicenseDetails( {
						licenseKey: response.data?.data?.license_key,
						licenseStatus: response.data?.data?.license_status,
						licenseStatusText: response.data?.data?.license_status_text
					} )
				}
			})
			.catch( (error) => {
				if ( error.response ) {
					if ( ! isEmpty( getParsedAjaxError( error.response ) ) ) {
						this.setLicenseError( getParsedAjaxError( error.response ) );
						this.setState( { licenseStatus: error.response.data.data.status } )
						this.setState( { licenseStatusText: error.response.data.data.error_message } )
					} else {
						this.setLicenseError( error.response.statusText );
					}
				} else if (error.request) {
					this.setLicenseError( __( 'The request was made but no response was received.' ) );
				} else {
					this.setLicenseError( __( 'Something went wrong while making the request.' ) );
				}
			})
			.finally(() => {
				this.setLoading(false)
			});

	}

	/**
	 * Set the loading state.
	 *
	 * @param {boolean} loading
	 * @returns {void}
	 */
	setLoading = loading =>
		this.setState(state => ({
			loading: loading
		})
	);

	/**
	 * Show an error message or disable the message.
	 * @param {} message
	 * @returns
	 */
	setLicenseError = message =>
		this.setState( state => ({
			licenseError: message
	  	})
	);

	/**
	 * Toggle popover
	 */
	togglePopover() {
		this.setState(prevState => ({
			isStepDescriptionPopoverVisible: !prevState.isStepDescriptionPopoverVisible
		}));
	}

	/**
	 * Toggle popover
	 */
	toggleLicensePopover() {
		this.setState(prevState => ({
			licensePopoverVisible: !prevState.licensePopoverVisible
		}));
	}

	/**
	 * If a license is provided and the "next" button is clicked,
	 * trigger license activation. Otherwise go to next step.
	 *
	 * @param {string} action
	 */
	maybeHandleLicenseActivation( action = 'activate' ) {

		const { licenseKey } = this.state
		const { goToNextStep, stateActions } = this.props

		this.setLoading(true)

		// License activation is optional.
		if ( isEmpty(licenseKey) ) {
			return goToNextStep()
		}

		let formData = new FormData()

		formData.append('action', `barn2_wizard_${SetupWizardSettings.plugin_slug}_step_license_activation`)
		formData.append('nonce', SetupWizardSettings.nonce)
		formData.append('license_key', licenseKey)
		formData.append('license_action', action )

		axios.post(SetupWizardSettings.ajax, formData)
			.then( (response) => {
				if (has(response.data, 'data')) {
					const { status, status_text } = response.data.data

					this.setLicenseError( '' )
					this.setState( { licenseStatus: status } )
					this.setState( { licenseStatusText: status_text } )
				}
			})
			.catch( (error) => {
				if ( error.response ) {
					if ( ! isEmpty( getParsedAjaxError( error.response ) ) ) {
						this.setLicenseError( getParsedAjaxError( error.response ) );
						this.setState( { licenseStatus: error.response.data.data.status } )
						this.setState( { licenseStatusText: error.response.data.data.error_message } )
					} else {
						this.setLicenseError( error.response.statusText );
					}
				} else if (error.request) {
					this.setLicenseError( __( 'The request was made but no response was received.' ) );
				} else {
					this.setLicenseError( __( 'Something went wrong while making the request.' ) );
				}
			})
			.finally(() => {
				this.setLoading(false)
				const { licenseStatus, licenseKey, licenseStatusText } = this.state

				stateActions.updateLicenseDetails( {
					licenseKey: licenseKey,
					licenseStatus: licenseStatus,
					licenseStatusText: licenseStatusText
				} )

				if ( licenseStatus === 'active' ) {
					return goToNextStep()
				}
			});

	}

	render() {

		const {
			step,
		} = this.props;

		const {
			isStepDescriptionPopoverVisible,
			loading,
			licensePopoverVisible,
			licenseError,
			licenseStatusText,
			licenseStatus,
			licenseKey
		} = this.state;

		const descriptionClassNames = classNames({
			has_tooltip: !isEmpty(step.tooltip)
		})

		return (
			<div className="woocommerce-profile-wizard__store-details">
				<div className="woocommerce-profile-wizard__step-header">
					<Heading
						level="2"
						size="20"
						lineHeight="28px"
					>
						{isEmpty(step.heading) ? sprintf(__('Welcome to %s', ''), SetupWizardSettings.plugin_name) : step.heading}
					</Heading>

					<Text variant="body" as="p" className={descriptionClassNames}>
						{!isEmpty(step.description) &&
							<>
								{step.description}
							</>
						}
						{!isEmpty(step.tooltip) &&
							<>
								{
									isStepDescriptionPopoverVisible
										? <Button
											isTertiary
											label={step.tooltip}
											className="tooltip-btn"
											disabled
											onClick={() => {
												this.togglePopover()
											}}
										>
											<Icon icon={info} />
										</Button>
										: <Button
											isTertiary
											label={step.tooltip}
											className="tooltip-btn"
											onClick={() => {
												this.togglePopover()
											}}
										>
											<Icon icon={info} />
										</Button>
								}
							</>
						}
					</Text>
					{!isEmpty(step.tooltip) && isStepDescriptionPopoverVisible && (
						<Popover
							focusOnMount="container"
							position="bottom center"
							onClose={() => {
								setTimeout(() => {
									this.togglePopover()
								}, 100);
							}
							}
						>
							{step.tooltip}
						</Popover>
					)}
				</div>
				<Card>
					<CardBody>

						{ ! loading
							&& isEmpty( licenseError )
							&& ! isEmpty( licenseStatusText )
							&&
								<Notice status={ licenseStatus === 'active' ? 'success' : 'error' } isDismissible={ false }>{ parse( licenseStatusText ) }</Notice>
						}

						{ ! loading && ! isEmpty( licenseError ) && <Notice status="error" isDismissible={ false }>{ parse( licenseError ) }</Notice> }

						<div className="license-input-col">
							<TextControl
								placeholder={ step.fields.license_key.label }
								disabled={ loading }
								value={ licenseKey }
								onChange={ (value) => {
									this.setState( { licenseKey: value } )
								} }
							/>
							{
								licenseStatus === 'active' &&
								<div style={{ 'margin': '10px 0 0 0' }}>
									<Button disabled={ loading } isSecondary onClick={ () => this.maybeHandleLicenseActivation( 'deactivate' ) } className="">
										{ __( 'Deactivate' ) }
									</Button>
								</div>
							}
						</div>

						{ ! isEmpty( step.fields.license_key.description ) &&
							<>
							<div className="woocommerce-profile-wizard__footer woocommerce-profile-wizard__store-details license-info">
								<p className="description" style={ { margin: '0 5px 0 0' } }>{ step.fields.license_key.description }</p>
								{
									licensePopoverVisible
									? <Button
										isTertiary
										label={ '' }
										className="tooltip-btn"
										disabled
										onClick={ () => {
											this.toggleLicensePopover()
										} }
									>
										<Icon icon={ info } />
									</Button>
									: <Button
										isTertiary
										label={ '' }
										className="tooltip-btn"
										onClick={ () => {
											this.toggleLicensePopover()
										} }
									>
										<Icon icon={ info } />
									</Button>
								}
								{ licensePopoverVisible && (
									<Popover
										focusOnMount="container"
										position="bottom center"
										onClose={ () =>
											{
												setTimeout(() => {
													this.toggleLicensePopover()
												}, 100);
											}
										}
									>
										{ SetupWizardSettings.license_tooltip }
									</Popover>
								) }
							</div>
							</>
						}

					</CardBody>

					<CardFooter justify="center">
						<Button isPrimary isBusy={loading} disabled={loading} onClick={() => this.maybeHandleLicenseActivation()}>
							{__('Continue', 'woocommerce-admin')}
						</Button>
					</CardFooter>
				</Card>
			</div>
		);
	}
}

export default LicenseActivation
