/**
 * External dependencies
 */
import { Fragment, useState } from '@wordpress/element';
import { __, sprintf } from '@wordpress/i18n';
import { has, isEmpty } from 'lodash';
import { addQueryArgs } from '@wordpress/url';
import { SetupWizardSettings } from '../utilities'
import { Button, Popover, Tooltip } from '@wordpress/components';
import { Link, Pill } from '@woocommerce/components';
import { Icon, info } from '@wordpress/icons';
import classNames from 'classnames';

export default function ProductLabel( {
	plugin,
	disabled,
	is_access_pass,
} ) {

	const label = plugin.name
	const price = plugin.price

	const [ popoverEnabled, setPopoverEnabled ] = useState(false);

	const checkboxClasses = classNames(
		'woocommerce-product-wizard__product-types-label',
		{
			disabled: disabled
		}
	)

	return (
		<Fragment>
			<span className={ checkboxClasses }>
				{ label }
			</span>
			{
				is_access_pass &&
				<Button
					isTertiary
					onClick={ () => { setPopoverEnabled( true ); } }
				>
					<Icon icon={ info } />
				</Button>
			}
			{ popoverEnabled && is_access_pass && (
				<Popover
					focusOnMount="container"
					position="top center"
					onClose={ () => setPopoverEnabled( false ) }
				>
					{ __( 'You will pay a reduced price for the upgraded license to cover the rest of the year - this is the difference between what you paid originally and the cost of the upgrade, pro-rated.' ) }
				</Popover>
			) }
			{ ! isEmpty( price ) &&
				<Tooltip position="bottom center">
					<Pill>
						{ is_access_pass
							&& __( 'Upgrade for' ) + ' '
						}
						{
							is_access_pass && has( plugin, 'original_price' ) && ! isEmpty( plugin.original_price ) &&
							<span className="original-price">{ plugin.original_price }</span>
						}
						{ price }
					</Pill>
				</Tooltip>
			}
		</Fragment>
	);
}
