/**
 * WordPress dependencies
 */
import { CardBody } from '@wordpress/components';
import { __ } from '@wordpress/i18n';

/**
 * External dependencies
 */
import parse from 'html-react-parser';

export default function ListField( props ) {
	const { field, fieldKey, cardClasses } = props;

	return (
		<CardBody key={ fieldKey } className={ cardClasses }>
			<ul className="b2-bulleted-list">
				{ Object.keys( field.items ).map( function ( listKey ) {
					return (
						<li key={ listKey }>
							{ parse( field.items[ listKey ] ) }
						</li>
					);
				} ) }
			</ul>
		</CardBody>
	);
}
