/**
 * External dependencies
 */
import { __, sprintf } from '@wordpress/i18n';
import { doAction } from '@wordpress/hooks';
import {
	Button,
	Popover,
	Spinner,
	__experimentalText as Text,
	__experimentalHeading as Heading
} from '@wordpress/components';
import { Component } from '@wordpress/element';
import { Icon, info } from '@wordpress/icons';
import { isEmpty } from 'lodash';
import parse from 'html-react-parser';
import classNames from 'classnames';
import axios from 'axios';

/**
 * Internal dependencies
 */
import { SetupWizardSettings } from '../utilities'

class WizardHeader extends Component {
	constructor(props) {
		super(props);
		const { heading, description, tooltip } = props.step;

		this.state = {
			heading,
			description,
			tooltip,
			isStepDescriptionPopoverVisible: false,
			isLoading: false,
		};
	}

	componentDidMount() {
		doAction('barn2_wizard_step_header_mounted', this);

		// Query the api if on the upsell step.
		this.maybePullDescriptionFromApi()
	}

	componentDidUpdate(prevProps) {
		if (this.props.step.key !== prevProps.step.key) {
			doAction('barn2_wizard_step_header_updated', this);
		}
	}

	togglePopover() {
		this.setState(prevState => ({
			isStepDescriptionPopoverVisible: !prevState.isStepDescriptionPopoverVisible
		}));
	}

	/**
	 * Adjust loading state of the description.
	 *
	 * @param {boolean} loading
	 */
	setLoading( loading ) {
		this.setState(prevState => ({
			isLoading: loading
		}));
	}

	/**
	 * Programmatically set a description for the header.
	 *
	 * @param {string} description
	 */
	setDescription( description ) {
		this.setState(prevState => ({
			description: description
		}));
	}

	/**
	 * Query the barn2 api and retrieve the AAP description
	 * if we're on the upsell step.
	 *
	 * @returns {void}
	 */
	async maybePullDescriptionFromApi() {
		const { key } = this.props.step

		if ( key !== 'more' ) {
			return;
		}

		this.setLoading( true )

		try {
			const response = await axios.get(SetupWizardSettings.barn2_api, {
				params: {
					plugin: SetupWizardSettings.plugin_slug
				}
			});

			const { aap_description } = response.data

			if ( ! isEmpty( aap_description ) ) {
				this.setDescription( aap_description )
			}

			this.setLoading(false)

		} catch (err) {
			this.setLoading( false )
			console.error(err)
		}
	}

	render() {
		const {
			heading,
			description,
			tooltip,
			isStepDescriptionPopoverVisible,
			isLoading
		} = this.state;

		const descriptionClassNames = classNames({
			has_tooltip: ! isEmpty( tooltip )
		})

		return (
			<>
				<Heading
					level="2"
					size="20"
					lineHeight="28px"
				>
					{ isEmpty( heading ) ? sprintf( __( 'Welcome to %s', '' ), SetupWizardSettings.plugin_name ) : heading }
				</Heading>
				<Text variant="body" as="p" className={ descriptionClassNames }>
					{ ! isEmpty( description ) &&
						<>
							{ isLoading ? <Spinner /> : parse( description ) }
						</>
					}
					{ ! isEmpty( tooltip ) &&
							<>
								{
									isStepDescriptionPopoverVisible
									? <Button
										isTertiary
										label={ tooltip }
										className="tooltip-btn"
										disabled
										onClick={ () => {
											this.togglePopover()
										} }
									>
										<Icon icon={ info } />
									</Button>
        							: <Button
										isTertiary
										label={ tooltip }
										className="tooltip-btn"
										onClick={ () => {
											this.togglePopover()
										} }
									>
										<Icon icon={ info } />
									</Button>
								}
							</>
					}
				</Text>
				{ ! isEmpty( tooltip ) && isStepDescriptionPopoverVisible && (
					<Popover
						focusOnMount="container"
						position="bottom center"
						onClose={ () =>
							{
								setTimeout(() => {
									this.togglePopover()
								}, 100);
							}
						}
					>
						{ parse( tooltip ) }
					</Popover>
				) }
			</>
		);
	}
}

export default WizardHeader
