/**
 * WordPress dependencies
 */
import {
	CardBody,
	CardFooter,
	FlexItem as MaybeFlexItem,
	CheckboxControl,
} from '@wordpress/components';
import { __ } from '@wordpress/i18n';
import { has, isEmpty } from 'lodash';

/**
 * External dependencies
 */
import parse from 'html-react-parser';

/**
 * Internal dependencies
 */
import FieldLabel from '../components/fieldLabel';

export default function CheckboxField( props ) {
	const {
		field,
		fieldKey,
		value,
		getInputProps,
		isSubmitting,
		cardClasses,
	} = props;

	const CheckboxWrapper =
		has( field, 'border' ) && field.border === false
			? CardBody
			: CardFooter;

	const FlextItemSubstitute = ( { children, align } ) => {
		const style = {
			display: 'flex',
			'justify-content': align ? 'center' : 'flex-start',
		};
		return <div style={ style }>{ children }</div>;
	};
	const FlexItem = MaybeFlexItem || FlextItemSubstitute;

	return (
		<CheckboxWrapper key={ fieldKey } className={ cardClasses }>
			<FlexItem>
				<div className="woocommerce-profile-wizard__client">
					{ has( field, 'title' ) && (
						<FieldLabel
							label={ field.title }
							premium={ field.premium }
							type={ field.type }
						/>
					) }

					<CheckboxControl
						{ ...getInputProps( fieldKey ) }
						label={ field.label }
						disabled={ field.premium || isSubmitting }
						checked={ value }
					/>

					{ has( field, 'description' ) &&
						! isEmpty( field.description ) && (
							<p className="input-description">
								{ parse( field.description ) }
							</p>
						) }
				</div>
			</FlexItem>
		</CheckboxWrapper>
	);
}
