import { useStateMachine } from 'little-state-machine';

/**
 * Store the forms data.
 *
 * @param {object} state
 * @param {object} payload
 * @returns {object}
 */
export const updateDataStore = (state, payload) => {
	return {
		...state,
		...payload
	};
}

/**
 * Store license details.
 *
 * @param {object} state
 * @param {object} payload
 * @returns {object}
 */
export const updateLicenseDetails = (state, payload) => {
	return {
		...state,
		...payload
	};
}

/**
 * Higher order component that allows usage of hooks
 * in class components.
 *
 * @param {object} Component
 * @returns {object}
 */
export const withMyHook = (Component) => {
	return function WrappedComponent(props) {
		const { actions, state } = useStateMachine({ updateDataStore, updateLicenseDetails });
		return <Component {...props} stateActions={ actions } dataStore={ state }/>;
	}
}
