/**
 * WordPress dependencies
 */
import { __ } from '@wordpress/i18n';
import { applyFilters, doAction } from '@wordpress/hooks';
import {
	Button,
	Card,
	CardBody,
	CardFooter,
	Notice,
} from '@wordpress/components';
import { Component } from '@wordpress/element';
import { isEmpty, map, has, forEach } from 'lodash';

/**
 * WooCommerce dependencies
 */
import { Form } from '@woocommerce/components';

/**
 * External dependencies
 */
import axios from 'axios';
import parse from 'html-react-parser';
import qs from 'qs';
import checkConditions from 'json-conditions';
import classNames from 'classnames';

/**
 * Internal dependencies
 */
import WizardHeader from '../components/header';
import {
	SetupWizardSettings,
	getParsedAjaxError,
	getWizardValues,
} from '../utilities';
import {
	CheckboxField,
	HeadingField,
	ImageField,
	ListField,
	MinMaxField,
	NumberField,
	RadioField,
	SelectField,
	TextRepeaterField,
	TextField,
	TextareaField,
} from '../fields';

class WithForm extends Component {
	constructor( props ) {
		super( props );
		this.validateForm = this.validateForm.bind( this );
		this.onSubmit = this.onSubmit.bind( this );
		this.onChange = this.onChange.bind( this );

		this.state = {
			errorMessage: false,
			isSubmitting: false,
			hidden: {},
			continueButtonText: __( 'Continue' ),
		};

		const { getValues } = this.props;

		this.initialValues = getValues();
	}

	componentDidMount() {
		doAction('barn2_wizard_withform_mounted', this)
	}

	componentDidUpdate(prevProps) {
		if (this.props.step.key !== prevProps.step.key) {
			doAction('barn2_wizard_withform_updated', this);
		}
	}

	/**
	 * Set submitting
	 * @param {} submitting
	 * @returns
	 */
	setSubmitting = ( submitting ) =>
		this.setState( ( state ) => ( {
			isSubmitting: submitting,
		} ) );

	/**
	 * Set error message
	 * @returns
	 */
	setErrorMessage = ( message ) =>
		this.setState( ( state ) => ( {
			errorMessage: message,
		} ) );

	/**
	 * Set the continue button text in the state.
	 *
	 * @param {string} text
	 * @returns
	 */
	setContinueText = ( text ) =>
		this.setState( ( state ) => ( {
			continueButtonText: text,
		} ) );

	/**
	 * Get the fields of the step.
	 * @returns array
	 */
	getFields() {
		return this.props.step.fields;
	}

	/**
	 * Validate the form values and show errors if needed.
	 *
	 * @param {*} values
	 * @returns
	 */
	validateForm( values ) {
		const step = this.props.step.key;
		const fields = this.props.step.fields;
		const errors = {};

		map( fields, function ( field, field_key ) {
			if ( has( field, 'required' ) && field.required === true ) {
				if ( field.type === 'checkbox' ) {
					if (
						values[ field_key ] !== 'undefined' &&
						values[ field_key ] !== true
					) {
						errors[ field_key ] = __( 'This field is required.' );
					}
				} else {
					if ( ! values[ field_key ].trim().length ) {
						errors[ field_key ] = __( 'This field is required.' );
					}
				}
			}
		} );

		return applyFilters(
			'barn2_setup_wizard_validation_errors',
			errors,
			step,
			fields
		);
	}

	/**
	 * Determine if the field should be visible.
	 *
	 * @param {object} field contains the details about the field being processed.
	 * @param {string} id id of the field being processed.
	 * @param {object} values list of all values submitted throughout the whole wizard.
	 * @returns bool
	 */
	isFieldVisible( field, id, values ) {
		if ( has( field, 'conditions' ) ) {
			const rules = [];
			const conditions = field.conditions;
			const conditionsComparison = has( field, 'comparison' ) ? field.comparison : 'ALL'

			forEach( conditions, ( settings, condition_field_id ) => {
				rules.push( {
					property: condition_field_id,
					op: settings.op,
					value: settings.value,
				} );
			} );

			if ( ! isEmpty( rules ) ) {
				const shouldLog = has( field, 'debug' ) ? console.log : false;

				return checkConditions(
					{
						rules: rules,
						satisfy: conditionsComparison,
						log: shouldLog,
					},
					values
				);
			}
		}

		return true;
	}

	/**
	 * Trigger an hook when the value of a field within the form is changed.
	 *
	 * @param {object} field details of the field that was changed
	 * @param {object} values all the values from the state of the form
	 * @param {bool} valid whether or not validation errors were found
	 */
	onChange( field, values, valid ) {
		const step = this;
		const stepKey = this.props.step.key;

		const { updateValue, stateActions } = this.props;
		updateValue( field.name, field.value );

		stateActions.updateDataStore( values );

		doAction(
			`barn2_wizard_on_field_change`,
			field,
			step,
			stepKey,
			values,
			valid
		);
	}

	/**
	 * Submit the form via ajax
	 */
	onSubmit( values ) {
		this.setSubmitting( true );
		this.setErrorMessage( false );

		const handler = this;
		const step_key = this.props.step.key;

		const { goToNextStep } = this.props;

		let successful = false;

		axios
			.post(
				SetupWizardSettings.ajax,
				qs.stringify( {
					...values,
					action: `barn2_wizard_${ SetupWizardSettings.plugin_slug }_step_${ step_key }`,
					nonce: SetupWizardSettings.nonce,
				} )
			)
			.then( function ( response ) {
				if ( response.data.data ) {
					forEach( response.data.data, ( value, key ) => {
						if ( has( values, key ) ) {
							handler.props.updateValue( key, value );
						}
					});

					handler.props.stateActions.updateDataStore( values );
				}

				successful = true;
			} )
			.catch( function ( error ) {
				successful = false;
				if ( error.response ) {
					if ( ! isEmpty( getParsedAjaxError( error.response ) ) ) {
						handler.setErrorMessage(
							getParsedAjaxError( error.response )
						);
					} else {
						handler.setErrorMessage( error.response.statusText );
					}
				} else if ( error.request ) {
					handler.setErrorMessage(
						__(
							'The request was made but no response was received.'
						)
					);
				} else {
					handler.setErrorMessage(
						__( 'Something went wrong while making the request.' )
					);
				}
			} )
			.then( function ( response ) {
				handler.setSubmitting( false );
			} )
			.finally( () => {
				if ( successful ) {
					goToNextStep();
				}
			} );
	}

	render() {
		const { step, goToNextStep, goToPrevStep, isFirstStep, isLastStep } = this.props;

		const {
			isSubmitting,
			errorMessage,
			continueButtonText,
			hidden,
		} = this.state;

		const instance = this;

		const fieldComponents = {
			checkbox: CheckboxField,
			heading: HeadingField,
			title: HeadingField,
			image: ImageField,
			list: ListField,
			min_max: MinMaxField,
			number: NumberField,
			radio: RadioField,
			select: SelectField,
			text_repeater: TextRepeaterField,
			text: TextField,
			textarea: TextareaField,
		};

		return (
			<div className="woocommerce-profile-wizard__store-details">
				<div className="woocommerce-profile-wizard__step-header">
					<WizardHeader step={ step } stepProps={ this.props } />
				</div>

				<Form
					initialValues={ this.initialValues }
					onSubmit={ this.onSubmit }
					validate={ this.validateForm }
					onChange={ this.onChange }
				>
					{ ( {
						getInputProps,
						handleSubmit,
						values,
						isValidForm,
						setValue,
					} ) => (
						<Card>
							{ ! isEmpty( errorMessage ) && ! isSubmitting && (
								<CardBody className="form-error-message-container">
									<Notice
										status="error"
										isDismissible={ false }
									>
										{ parse( errorMessage ) }
									</Notice>
								</CardBody>
							) }

							{ map( step.fields, function ( field, field_key ) {
								if (
									hidden[ field_key ] ||
									! instance.isFieldVisible(
										field,
										field_key,
										values
									) ||
									! fieldComponents.hasOwnProperty(
										field.type
									)
								) {
									return;
								}

								const type = field.type;
								let value = values[ field_key ] || false;

								const fieldClasses =
									has( field, 'classes' ) &&
									! isEmpty( field.classes )
										? field.classes
										: [];

								const cardClasses = classNames(
									{
										'checkbox-wrapper': type === 'checkbox',
										'premium-only': field.premium === true,
									},
									fieldClasses
								);

								const FieldComponent =
									fieldComponents[ field.type ];

								return (
									<FieldComponent
										key={ field_key }
										field={ field }
										fieldKey={ field_key }
										value={ value }
										getInputProps={ getInputProps }
										isSubmitting={ isSubmitting }
										cardClasses={ cardClasses }
										setValue={ setValue }
									/>
								);
							} ) }

							<CardFooter justify="center">
								{ ! isFirstStep() &&
									<Button
										onClick={ () => goToPrevStep() }
										isSecondary
										isBusy={ this.state.isSubmitting }
										disabled={ this.state.isSubmitting }
									>
										{ __( 'Back', 'woocommerce-admin' ) }
									</Button>
								}

								<Button
									isPrimary
									onClick={ () => {
										const errors = this.validateForm(
											values
										);
										const firstError =
											errors[
												Object.keys( errors )[ 0 ]
											];

										if ( ! isEmpty( firstError ) ) {
											this.setErrorMessage( firstError );
											return;
										}

										this.onSubmit( values );
									} }
									isBusy={ this.state.isSubmitting }
									disabled={ this.state.isSubmitting }
								>
									{ continueButtonText }
								</Button>
							</CardFooter>
						</Card>
					) }
				</Form>
			</div>
		);
	}
}

export default WithForm;
