import { __ } from '@wordpress/i18n';
import React, { useState, setState } from 'react';
import { Component } from '@wordpress/element';
//import { TextControl } from '@woocommerce/components';
import { has, isEmpty } from 'lodash';
import {
	Button,
	Popover,
	Spinner,
	Notice,
	TextControl,
} from '@wordpress/components';
import { Icon, info } from '@wordpress/icons';
import { Container, Row, Col } from 'react-grid-system';
import axios from 'axios';
import parse from 'html-react-parser';

/**
 * Internal dependencies
 */
import { SetupWizardSettings } from '../utilities';
import { getParsedAjaxError } from '../utilities';

export default class LicenseForm extends Component {

	constructor( props ) {
		super( props );
		this.state = {
			isPopoverVisible: false,
			isLicenseLoading: false,
			licenseError: false,
			licenseStatus: false,
			licenseStatusText: '',
			licenseKey: '',
		};
	}

	/**
	 * Setup initial values of the form and license status
	 */
	componentDidMount() {
		const { dataStore } = this.props

		if ( has( SetupWizardSettings, 'license_key' ) && ! isEmpty( SetupWizardSettings.license_key ) ) {
			this.setState( { licenseKey: SetupWizardSettings.license_key } )
		}

		// Save the license status key
		if ( has( SetupWizardSettings, 'license_status' ) && ! isEmpty( SetupWizardSettings.license_status ) ) {
			this.setState( { licenseStatus: SetupWizardSettings.license_status } )
		}

		// Save the license status text
		if ( has( SetupWizardSettings, 'license_status_text' ) && ! isEmpty( SetupWizardSettings.license_status_text ) ) {
			this.setState( { licenseStatusText: SetupWizardSettings.license_status_text } )
		}

		if ( ! has( dataStore, 'licenseKey' ) && this.props.eventable ) {
			this.props.onLicenseUpdate( {
				licenseStatus: SetupWizardSettings.license_status,
				licenseKey: SetupWizardSettings.license_key,
				licenseStatusText: SetupWizardSettings.license_status_text
			} )
		}
	}

	/**
	 * Adjust loading status of verification.
	 *
	 * @param {*} loading
	 * @returns
	 */
	setLoading = loading =>
		this.setState( state => ({
			isLicenseLoading: loading
	  	})
	);

	/**
	 * Show an error message or disable the message.
	 * @param {} message
	 * @returns
	 */
	setLicenseError = message =>
		this.setState( state => ({
			licenseError: message
	  	})
	);

	/**
	 * Toggle the popopver.
	 *
	 * @returns {void}
	 */
	togglePopover() {
		this.setState(prevState => ({
			isPopoverVisible: !prevState.isPopoverVisible
		}));
	}

	/**
	 * Request license validation via ajax.
	 */
	requestLicense = ( action = 'activate' ) => {

		this.setLoading( true )
		this.setLicenseError( false )

		const licenseHandler = this

		const {
			onLicenseUpdate,
			eventable,
		} = this.props

		let formData = new FormData()

		formData.append( 'action', `barn2_wizard_${ SetupWizardSettings.plugin_slug }_step_license_activation` )
		formData.append( 'nonce', SetupWizardSettings.nonce )
		formData.append( 'license_key', this.state.licenseKey )
		formData.append( 'license_action', action )

		axios.post( SetupWizardSettings.ajax, formData )
			.then(function (response) {
				if ( has( response.data, 'data' ) ) {
					licenseHandler.setState( { licenseStatus: response.data.data.status } )
					licenseHandler.setState( { licenseStatusText: response.data.data.status_text } )
				}
			})
			.catch(function (error) {
				if ( error.response ) {
					if ( ! isEmpty( getParsedAjaxError( error.response ) ) ) {
						licenseHandler.setLicenseError( getParsedAjaxError( error.response ) );
						licenseHandler.setState( { licenseStatus: error.response.data.data.status } )
						licenseHandler.setState( { licenseStatusText: error.response.data.data.error_message } )
					} else {
						licenseHandler.setLicenseError( error.response.statusText );
					}
				} else if (error.request) {
					licenseHandler.setLicenseError( __( 'The request was made but no response was received.' ) );
				} else {
					licenseHandler.setLicenseError( __( 'Something went wrong while making the request.' ) );
				}
			})
			.finally(() => {
				licenseHandler.setLoading(false)

				if ( eventable === true ) {
					onLicenseUpdate( licenseHandler.state )
					return
				}

				window.location.reload()
			});
	};

	render() {
		const props = this.props;

		const {
			description,
			label,
			tooltip,
			disabled
		} = props

		const {
			isPopoverVisible,
			isLicenseLoading,
			licenseError,
			licenseStatusText,
			licenseStatus,
			licenseKey
		} = this.state;

		return (
			<div className="barn2-license-validation-control wizard-body">
				<Container fluid={ true } className="fields-row">

					<Row align={ 'stretch' } gutterWidth={ 10 } className="license-input-row">
						<Col sm={9} style={ { margin: '0' } } className="license-input-col">
							<TextControl
								placeholder={ label }
								disabled={ isLicenseLoading || disabled }
								value={ licenseKey }
								onChange={ (value) => {
									this.setState( { licenseKey: value } )
								} }
							/>
						</Col>
						<Col sm={3}>
							<Button disabled={ isLicenseLoading || disabled } isSecondary onClick={ () => this.requestLicense() } className="muriel-component license-btn">
								{ __( 'Activate' ) }
							</Button>
						</Col>
					</Row>

					{ ! isLicenseLoading
						&& isEmpty( licenseError )
						&& ! isEmpty( licenseStatusText )
						&& ! disabled
						&&
							<Notice status={ licenseStatus === 'active' ? 'success' : 'error' } isDismissible={ false }>{ parse( licenseStatusText ) }</Notice>
					}

					{ ! isLicenseLoading && ! disabled && ! isEmpty( licenseError ) && <Notice status="error" isDismissible={ false }>{ parse( this.state.licenseError ) }</Notice> }

					{
						licenseStatus === 'active' && ! disabled &&
						<>
							<Button disabled={ isLicenseLoading || disabled } isSecondary onClick={ () => this.requestLicense( 'check' ) } style={ { marginRight: '0.5rem' } }>
								{ __( 'Check' ) }
							</Button>
							<Button disabled={ isLicenseLoading || disabled } isSecondary onClick={ () => this.requestLicense( 'deactivate' ) } className="">
								{ __( 'Deactivate' ) }
							</Button>
						</>
					}

					{ ! isEmpty( description ) &&
						<div className="woocommerce-profile-wizard__footer woocommerce-profile-wizard__store-details license-info">
							<p className="description" style={ { margin: '0' } }>{ description }</p>
							{
								isPopoverVisible
								? <Button
									isTertiary
									label={ '' }
									className="tooltip-btn"
									disabled
									onClick={ () => {
										this.togglePopover()
									} }
								>
									<Icon icon={ info } />
								</Button>
        						: <Button
									isTertiary
									label={ '' }
									className="tooltip-btn"
									onClick={ () => {
										this.togglePopover()
									} }
								>
									<Icon icon={ info } />
								</Button>
							}
							{ isPopoverVisible && (
								<Popover
									focusOnMount="container"
									position="bottom center"
									onClose={ () =>
										{
											setTimeout(() => {
												this.togglePopover()
											}, 100);
										}
									}
								>
									{ tooltip }
								</Popover>
							) }
							{ isLicenseLoading && <Spinner></Spinner> }
						</div>
					}

				</Container>
			</div>
		);
	}
}
